<template>
    <div class="row">
        <div class="col-md-12">
            <div class="card shadow rounded">
                <div class="card-header">
                    <div class="row d-flex align-items-center">
                        <div class="col-md-6">
                            <h5>Datos Generales</h5>
                        </div>
                        <div class="col-md-6">
                            <div class="d-flex excel-div">
                                <input 
                                    type="file" 
                                    ref="fileInput" 
                                    @change="onFileChange" 
                                    class="form-control excel-input"
                                    style="height: 100%; width:72.5% ; padding-top: 9px; padding-bottom: 9px;"
                                    accept=".xlsx, .xls" 
                                />
                                <button class="btn btn-success btn-lg ml-auto rounded excel-btn"
                                    @click="uploadFile()">Cargar
                                    Excel</button>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="card-body mb-0">
                    <div class="row">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label">N de Expediente</label>
                                <input 
                                    type="text" 
                                    class="form-control" 
                                    placeholder="5670-2017-0-1708-JM-LA-01"
                                    v-model="datosgenerales.exp_numero" 
                                    @input="errores.exp_numero = false"
                                    @blur="validarNumeroExpediente" 
                                    :class="{'has-error': errores.exp_numero }" 
                                />
                                <span class="error-message" v-if="errores.exp_numero">Ingrese un numero de expediente.</span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label">Fecha de Inicio</label>
                                <input 
                                    class="form-control" 
                                    type="date" 
                                    v-model="datosgenerales.exp_fecha_inicio" 
                                    @input="validarFechaInicio" 
                                    @blur="validarFechaInicio" 
                                    :class="{'has-error': errores.exp_fecha_inicio}"/>
                                <span class="error-message" v-if="errores.exp_fecha_inicio">Ingrese una fecha de inicio.</span>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label">Pretensin</label>
                                <select type="text" class="form-control" @change="validarPretension" 
                                    v-model="datosgenerales.exp_pretencion"
                                    :class="{ 'has-error': errores.exp_pretencion }">
                                    <option value="" disabled selected class="text-secondary">-- Seleccione una opcin</option>
                                    <option v-for="pretension in pretenciones" :value="pretension.pre_id"
                                        :key="pretension.pre_id">{{
                                            pretension.pre_nombre }}</option>
                                </select>
                                <span class="error-message" v-if="errores.exp_pretencion">Seleccione una Pretension.</span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label">Monto de Pretensin</label>
                                <input type="number" class="form-control" placeholder="S/ 5500.00"
                                    v-model="datosgenerales.exp_monto_pretencion"
                                    @input="validarMontoPretension" @blur="validarMontoPretension"
                                    :class="{ 'has-error': errores.exp_monto_pretencion }" />
                                <span class="error-message" v-if="errores.exp_monto_pretencion">Ingrese un monto de pretension.</span>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label">Materia</label>
                                <select class="form-control" v-model="datosgenerales.exp_materia"
                                    :class="{ 'has-error': errores.exp_materia }" @change="validarMateria" >
                                    <option value="" disabled selected class="py-5">-- Seleccione una opcin</option>
                                    <option v-for="(materia) in materias" :key="materia.mat_id" :value="materia.mat_id"
                                        class="py-3">
                                        {{ materia.mat_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.exp_materia">Seleccione una Materia.</span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label">Distrito Judicial</label>
                                <select class="form-control" @change="seleccionarDistrito"
                                    v-model="datosgenerales.exp_distrito_judicial"
                                    :class="{ 'has-error': errores.exp_distrito_judicial }">
                                    <option value="" disabled selected>-- Seleccione una opcin</option>
                                    <option v-for="distrito in distritos" :value="distrito.judis_id">
                                        {{ distrito.judis_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.exp_distrito_judicial">Seleccione un Distrito Judicial.</span>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label">Instancia</label>
                                <select class="form-control" v-model="datosgenerales.exp_instancia"
                                    @change="cargarespecialidades" :class="{ 'has-error': errores.exp_instancia }">
                                    <option value="" disabled selected>-- Seleccione una opcin</option>
                                    <option v-for="instancias in instancia" :value="instancias.ins_id">
                                        {{ instancias.ins_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.exp_instancia">Seleccione una Instancia.</span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label">Especialidad</label>
                                <select class="form-control" v-model="datosgenerales.exp_especialidad"
                                    :class="{ 'has-error': errores.exp_especialidad }" @change="validarEspecialidad">
                                    <option value="" disabled selected>-- Seleccione una opcin</option>
                                    <option v-for="especialidades in especialidad" :value="especialidades.esp_id">
                                        {{ especialidades.esp_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.exp_especialidad">Seleccione una Especialidad.</span>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label mb-1">rgano Jurisdiccional</label>
                                <select class="form-control" v-model="datosgenerales.exp_juzgado"
                                    :class="{ 'has-error': errores.exp_juzgado }" @change="validarJuzgado">
                                    <option value="" disabled selected>-- Seleccione una opcin</option>
                                    <option v-for="juzgado in juzgados"
                                        :key="juzgado.co_id" :value="juzgado.co_id">
                                        {{ juzgado.co_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.exp_juzgado">Seleccione un Organo Jurisdiccional.</span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label mb-1">Estado:</label>
                                <select class="form-control" v-model="datosgenerales.exp_estado_proceso" :class="{ 'has-error': errores.exp_estado_proceso }" @change="validarEstado">
                                    <option value="" disabled selected class="py-5">-- Seleccione una opcin</option>
                                    <option value="EN TRAMITE">En Trmite</option>
                                    <option value="EN EJECUCION">En Ejecucin</option>
                                    <option value="ARCHIVADO">Archivado</option>
                                </select>
                                <span class="error-message" v-if="errores.exp_estado_proceso">Selecione un estado para el Expediente.</span>
                            </div>
                        </div>
                    </div>
                    <div class="row" v-if="datosgenerales.exp_estado_proceso == 'EN EJECUCION'
                        || datosgenerales.exp_estado_proceso == 'ARCHIVADO'">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="MontoPretencion">Monto de Ejecucin 1</label>
                                <input v-model="datosgenerales.exp_monto_ejecucion1" type="number" class="form-control"
                                    id="MontoPretencion" placeholder="S/ 5500.00" />
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="MontoConsentido">Monto de Ejecucin 2</label>
                                <input v-model="datosgenerales.exp_monto_ejecucion2" type="number" class="form-control"
                                    id="MontoConsentido" placeholder="S/ 5000.00" />
                            </div>
                        </div>
                    </div>
                    <div class="row" v-if="datosgenerales.exp_estado_proceso == 'EN EJECUCION'
                        || datosgenerales.exp_estado_proceso == 'ARCHIVADO'">
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label" for="Costos">Inters 1</label>
                                <input v-model="datosgenerales.exp_interes1" type="number" class="form-control" id="int1"
                                    placeholder="S/ 450.00" />
                            </div>
                        </div>
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label" for="MontoPretencion">Inters 2</label>
                                <input v-model="datosgenerales.exp_interes2" type="number" class="form-control" id="int2"
                                    placeholder="S/ 500.00" />
                            </div>
                        </div>
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label" for="MontoPretencion">Costos</label>
                                <input v-model="datosgenerales.exp_costos" type="number" class="form-control" id="Costos"
                                    placeholder="S/ 2500.00" />
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12 d-flex justify-content-end" v-show="btnNext">
                            <button class="btn btn-primary btn-lg rounded" type="button" @click="siguienteParte">
                                Siguiente <i data-feather="arrow-right"></i>
                            </button>
                        </div>
                    </div>
                </div>
                <!-- <div class="card-footer d-flex justify-content-end" v-show="btnNext">
                    <button class="btn btn-primary btn-lg rounded" type="button" @click="siguienteParte">
                        Siguiente <i data-feather="arrow-right"></i>
                    </button>
                </div> -->
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { mapActions } from "vuex";
import feather from 'feather-icons';
import Swal from 'sweetalert2';
import Datepicker from 'vue-datepicker';
// import Swal from 'sweetalert2/dist/sweetalert2.js'
// import 'sweetalert2/src/sweetalert2.scss'

export default {
    name: "RegistroGeneral",
    components: {
        Datepicker
    },
    props: {
        exp: {},
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            fechaSeleccionada: new Date(),
            distritos: [],
            instancia: [],
            especialidad: [],
            materias: [],
            pretenciones: [],
            distritos: [],
            juzgados: [],

            datosgenerales: {
                exp_numero: "",
                exp_fecha_inicio: "",
                exp_pretencion: "",
                exp_materia: "",
                exp_distrito_judicial: "",
                exp_instancia: "",
                exp_especialidad: "",
                exp_monto_pretencion: "",
                exp_juzgado: "",
                exp_estado_proceso: "",
                exp_costos: "",
                exp_monto_ejecucion1: "",
                exp_monto_ejecucion2: "",
                exp_interes1: "",
                exp_interes2: "",

            },

            // Archivo seleccionado
            selectedFile: null,
            fileError: "",

            // Errores
            errores: {
                file: false,
                exp_numero: false,
                exp_fecha_inicio: false,
                exp_pretencion: false,
                exp_monto_pretencion: false,
                exp_materia: false,
                exp_distrito_judicial: false,
                exp_instancia: false,
                exp_especialidad: false,
                exp_juzgado: false,
                exp_estado_proceso: false
            },

            // Estado para mostrar/ocultar seccin de agregar juzgado
            mostrarAgregarJuzgado: false,

            // Estado para controlar el texto del botn de accin
            btnTipoAccion: 'Agregar',

            // Estado para habilitar/deshabilitar botn de siguiente
            btnNext: true
        };
    },

    created() {
        this.listarMaterias();
        this.listarPretensiones();
        this.datosgenerales = this.exp;
        this.cargardistritos();
        this.cargarinstancias();
        this.cargarespecialidades();
        this.cargarjuzgados();
    },

    methods: {
        ...mapActions(['get', 'post']),
        listarMaterias() {
            this.get({
                url: this.$store.getters.get__url + "/subject",
                token: this.$store.getters.get__token,
            })
                .then((response) => {
                    console.log(response);
                    this.materias = response.data;
                })
                .catch((error) => {
                    console.error("Error al intentar obtener los datos", error);
                });
        },
        listarPretensiones() {
            this.get({
                url: this.$store.getters.get__url + "/claim",
                token: this.$store.getters.get__token,
            })
                .then((response) => {
                    console.log(response);
                    this.pretenciones = response.data;
                })
                .catch((error) => {
                    console.error("Error al intentar obtener los datos", error);
                });
        },
        cargardistritos() {

            this.get({
                url: this.$store.getters.get__url + "/judicialdistrict",
                token: this.$store.getters.get__token,
                params: {}
            })
                .then((response) => {
                    console.log(response);
                    this.distritos = response.data;

                })
                .catch((errors) => { });
        },
        cargarinstancias() {
            if (this.datosgenerales.exp_distrito_judicial) {
                this.get({
                    url: this.$store.getters.get__url + "/instance",
                    token: this.$store.getters.get__token,
                    // params: {
                    //   judis_id: this.datosgenerales.exp_distrito_judicial,
                    // }
                })
                    .then((response) => {
                        console.log(response);
                        this.instancia = response.data;
                        this.validarDistritoJudical();
                        // this.datosgenerales.exp_instancia = "";
                    })
                    .catch((errors) => { });
            }
        },
        cargarjuzgados() {
            if (this.datosgenerales.exp_distrito_judicial) {
                this.post({
                    url: this.$store.getters.get__url + "/juzgado",
                    token: this.$store.getters.get__token,
                    params: {
                        judis_id: this.datosgenerales.exp_distrito_judicial,
                    }
                })
                    .then((response) => {
                        console.log('juzgados', response);
                        this.juzgados = response.data;
                        this.validarDistritoJudical();

                    })
                    .catch((errors) => { });
            }
        },

        seleccionarDistrito() {
            this.cargarinstancias();
            this.cargarjuzgados();
        },

        cargarespecialidades() {
            if (this.datosgenerales.exp_instancia) {
                this.get({
                    url: this.$store.getters.get__url + "/specialty",
                    token: this.$store.getters.get__token,
                    // params: {
                    //   ins_id: this.datosgenerales.exp_instancia,
                    // }
                })
                    .then((response) => {
                        this.especialidad = response.data;
                        this.validarInstancia();
                        // this.datosgenerales.exp_especialidad = "";
                        console.log(response);
                    })
                    .catch((errors) => { });
            }
        },
        listJuzgados() {
            this.get({
                url: this.$store.getters.get__url + "/juzgado",
                token: this.$store.getters.get__token,
            })
                .then((response) => {
                    if (response.state == 0) {
                        this.juzgados = response.data;
                        this.initDataTable;
                    } else {
                        console.log("error");
                    }
                })
                .catch((errors) => { });
        },
        getJuzgadoNombre(juzgado) {
            return juzgado.co_nombre ? juzgado.co_nombre.toUpperCase() : '';
        },
        AgregarJuzgado() {
            if (this.mostrarAgregarJuzgado) {
                this.mostrarAgregarJuzgado = false;
                this.btnTipoAccion = "Agregar";
                this.btnNext = true;
            } else {
                this.mostrarAgregarJuzgado = true;
                this.btnTipoAccion = "Cerrar";
                this.btnNext = false;
            }
        },
        GuardarJuzgado() {

        },
        alertaFaltaArchivo() {
            Swal.fire({
                // title: 'Error!',
                text: 'Tiene que seleccionar un archivo para cargar datos.',
                icon: 'warning',
                confirmButtonText: 'Aceptar',
                confirmButtonColor: '##7267EF',
            })
        },

        showSuccessAlert(message) {
            Swal.fire({
                title: "Archivo cargado correctamente",
                text: message,
                icon: "success"
            });
        },
        onFileChange(event) {
            this.selectedFile = event.target.files[0];
        },
        async uploadFile() {
            if (!this.selectedFile) {
                this.alertaFaltaArchivo();
                return;
            }

            let loadingAlert;

            try {
                loadingAlert = Swal.fire({
                    title: "Cargando archivo",
                    text: "Registrando expediente, por favor, espera...",
                    showConfirmButton: false,
                    allowOutsideClick: false,
                    didOpen: () => Swal.showLoading()
                });

                const formData = new FormData();
                formData.append('excel', this.selectedFile);
                const response = await axios.post(this.$store.getters.get__url + '/excel/cargar', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                        Authorization: 'Bearer ' + this.$store.getters.get__token
                    },
                    params: this.archivo
                });

                // Cerrar la alerta de carga
                if (loadingAlert) loadingAlert.close();

                Swal.fire({
                    title: "Archivo cargado correctamente",
                    text: "La carga se ha completado con xito.",
                    icon: "success"
                });
                console.log(response);
                this.$refs.fileInput.value = null;
                this.selectedFile = null;
                this.$router.push({ path: '/sisge/expediente/busqueda' });
            } catch (error) {
                Swal.fire({
                    title: "Error al cargar el archivo",
                    text: "Ha ocurrido un error al cargar el archivo.",
                    icon: "error"
                });

                if (loadingAlert) loadingAlert.close();

                console.error(error);
            }
        },

        validarCampo(campo, propiedadError) {
            if (this.datosgenerales[campo] === "") {
                this.errores[propiedadError] = true;
            } else {
                this.errores[propiedadError] = false;
            }
        },
        validarNumeroExpediente() {
            this.validarCampo('exp_numero', 'exp_numero');
        },
        validarFechaInicio() {
            this.validarCampo('exp_fecha_inicio', 'exp_fecha_inicio');
        },
        validarPretension() {
            this.validarCampo('exp_pretencion', 'exp_pretension');
        },
        validarMontoPretension() {
            this.validarCampo('exp_monto_pretencion', 'exp_monto_pretencion');
        },
        validarMateria() {
            this.validarCampo('exp_materia', 'exp_materia');
        },
        validarDistritoJudical() {
            this.validarCampo('exp_distrito_judicial', 'exp_distrito_judicial');
        },
        validarInstancia() {
            this.validarCampo('exp_instancia', 'exp_instancia');
        },
        validarEspecialidad() {
            this.validarCampo('exp_especialidad', 'exp_especialidad');
        },
        validarJuzgado() {
            this.validarCampo('exp_juzgado', 'exp_juzgado');
        },
        validarPretension() {
            this.validarCampo('exp_pretencion', 'exp_pretencion');
        },
        validarEstado() {
            this.validarCampo('exp_estado_proceso', 'exp_estado_proceso');
        },
        validarCampos() {
            this.validarNumeroExpediente();
            this.validarFechaInicio();
            this.validarPretension();
            this.validarMontoPretension();
            this.validarMateria();
            this.validarDistritoJudical();
            this.validarInstancia();
            this.validarEspecialidad();
            this.validarJuzgado();
            this.validarPretension();
            this.validarEstado();

            return !Object.values(this.errores).some(error => error);
        },
        siguienteParte() {
            if (this.validarCampos()) {
                this.$emit("guardardatosgenerales", this.datosgenerales);
                this.$emit("siguienteParte");
            } else {
                console.log('error');
            }
        },

    }
};
</script>

<style scoped>
.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    /* padding-left: 15px; */
    color: #EA4D4D;
    font-size: 12px;
}

@media only screen and (max-width: 600px) {
    .excel-div {
        display: block !important;
    }

    .excel-input {
        width: 100% !important;
        margin-top: 1em;
        margin-bottom: 1em;
    }

    .excel-btn {
        width: 100%;
    }
}
</style>
