<template>
    <div class="row">
        <div class="col-md-12">
            <div class="card shadow rounded">
                <div class="card-header">
                    <div class="row d-flex align-items-center">
                        <div class="col-xl-6 col-sm-12">
                            <h5>Partes Procesales</h5>
                        </div>
                        <div class="col-xl-6 col-sm-12">
                            <div class="d-flex justify-content-end">
                                <!-- <button class="btn btn-outline-success btn-lg rounded mx-2" type="button"
                                    style="width: 7.10rem;">
                                    Aadir
                                </button> -->
                                <button class="btn btn-outline-primary btn-lg rounded px-5" type="button"
                                    @click="mostrarModal">
                                    Lista
                                </button>

                            </div>
                        </div>
                    </div>
                </div>
                <div class="card-body">
                    <div class="row mb-3">
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label">Tipo de parte procesal</label>
                                <select class="form-control" v-model="tipo_procesal"
                                    :class="{ 'has-error': errores.tipo_procesal }">
                                    <option value="" disabled selected>-- Seleccione una opcin</option>
                                    <option value="DEMANDANTE">Demandante</option>
                                    <option value="DEMANDADO">Demandado</option>
                                </select>
                                <span class="error-message" v-if="errores.tipo_procesal">Complete este campo para
                                    continuar.</span>
                            </div>
                        </div>
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label">Tipo de persona</label>
                                <select class="form-control" v-model="tipo_persona"
                                    :class="{ 'has-error': errores.tipo_persona }">
                                    <option value="" disabled selected>-- Seleccione una opcin</option>
                                    <option value="NATURAL">Persona Natural</option>
                                    <option value="JURIDICA">Persona Juridica</option>
                                </select>
                                <span class="error-message" v-if="errores.tipo_persona">Complete este campo para
                                    continuar.</span>
                            </div>
                        </div>
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label">Condicin de demandante</label>
                                <select class="form-control" v-model="condicion1"
                                    :class="{ 'has-error': errores.condicion1 }">
                                    <option value="" disabled selected>-- Seleccione una opcin</option>
                                    <option value="DOCENTE UNIVERSITARIO">Docente Universitario</option>
                                    <option value="ESTUDIANTE">Estudiante</option>
                                    <option value="ADMINISTRATIVO">Administrativo</option>
                                    <option value="TERCERO">Tercero</option>
                                </select>
                                <span class="error-message" v-if="errores.condicion1">Complete este campo para
                                    continuar.</span>
                            </div>
                        </div>
                    </div>
                    <div class="row mb-3" v-if="tipo_persona != ''">
                        <div class="col-md-12">
                            <div class="alert alert-primary text-primary" role="alert">
                                Informacion Personal
                            </div>
                        </div>
                    </div>
                    <div v-if="tipo_persona === 'NATURAL'">
                        <div class="row">
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label" for="dni">DNI</label>
                                    <input type="text" class="form-control" id="dni" placeholder="12345678"
                                        v-model="pnat.nat_dni" @input="validarCampo(pnat.nat_dni, 'nat_dni')"
                                        :class="{ 'has-error': errores.nat_dni }" />
                                    <span class="error-message" v-if="errores.nat_dni">Complete este campo para
                                        continuar.</span>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Apellido
                                        Paterno</label>
                                    <input type="text" class="form-control" placeholder="Alarcon"
                                        v-model="pnat.nat_apellido_paterno"
                                        @input="validarCampo(pnat.nat_apellido_paterno, 'nat_apellido_paterno')"
                                        :class="{ 'has-error': errores.nat_apellido_paterno }">
                                    <span class="error-message" v-if="errores.nat_apellido_paterno">Complete este
                                        campo para continuar.</span>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Apellido
                                        Materno</label>
                                    <input type="text" class="form-control" placeholder="Ramirez"
                                        v-model="pnat.nat_apellido_materno"
                                        @input="validarCampo(pnat.nat_apellido_materno, 'nat_apellido_materno')"
                                        :class="{ 'has-error': errores.nat_apellido_materno }">
                                    <span class="error-message" v-if="errores.nat_apellido_materno">
                                        Complete este campo para continuar.
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Nombres</label>
                                    <input type="text" class="form-control" placeholder="Jhon Doe"
                                        v-model="pnat.nat_nombres" @input="validarCampo(pnat.nat_nombres, 'nat_nombres')"
                                        :class="{ 'has-error': errores.nat_nombres }">
                                    <span class="error-message" v-if="errores.nat_nombres">
                                        Complete este campo para continuar.
                                    </span>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Telefono</label>
                                    <input type="text" class="form-control" placeholder="980560345"
                                        v-model="pnat.nat_telefono">

                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Email
                                    </label>
                                    <input type="text" class="form-control" placeholder="example@gmail.com"
                                        v-model="pnat.nat_correo">

                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-if="tipo_persona === 'JURIDICA'">
                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label" for="ruc">RUC</label>
                                    <input type="text" class="form-control" id="ruc" placeholder="" v-model="pj.jur_ruc"
                                        @input="validarCampo(pjuc.jur_ruc, 'jur_ruc')"
                                        :class="{ 'has-error': errores.jur_ruc }">
                                    <span class="error-message" v-if="errores.jur_ruc">
                                        Ingrese un RUC.
                                    </span>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label" for="razonSocial">Nombre o Razon
                                        Social</label>
                                    <input type="text" class="form-control" id="razonSocial" placeholder=""
                                        v-model="pj.jur_razon_social"
                                        @input="validarCampo(pjuc.jur_razon_social, 'jur_razon_social')"
                                        :class="{ 'has-error': errores.jur_razon_social }">
                                    <span class="error-message" v-if="errores.jur_razon_social">
                                        Ingrese nombre o razon social.
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label" for="telefono">Telefono</label>
                                    <input type="text" class="form-control" id="telefono" placeholder=""
                                        v-model="pj.jur_telefono">
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label" for="email">Email
                                    </label>
                                    <input type="text" class="form-control" id="email" placeholder=""
                                        v-model="pj.jur_correo">
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row my-2">
                        <div class="col-md-12">
                            <div class="alert alert-primary text-primary" role="alert">
                                Direccion
                            </div>
                        </div>
                    </div>
                    <div class="row mt-3">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="departamento">Departamento</label>
                                <select class="form-control" id="departamento" v-model="direccion.dep_id"
                                    @change="cargarProvincias" :class="{ 'has-error': errores.dep_id }">
                                    <option value="" selected disabled>Seleccione una opcin</option>
                                    <option v-for="departamento in departamentos" :value="departamento.dep_id">{{
                                        departamento.dep_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.dep_id">
                                    Seleccione un departamento.
                                </span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="provincia">Provincia</label>
                                <select class="form-control" id="provincia" v-model="direccion.pro_id"
                                    @change="cargarDistritos" :class="{ 'has-error': errores.pro_id }">
                                    <option value="" selected disabled>Seleccione una opcin</option>
                                    <option v-for="provincia in provincias" :value="provincia.pro_id">{{
                                        provincia.pro_nombre }}</option>
                                </select>
                                <span class="error-message" v-if="errores.pro_id">
                                    Seleccione una provincia.
                                </span>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="distrito">Distrito</label>
                                <select class="form-control" id="distrito" v-model="direccion.dis_id"
                                    :class="{ 'has-error': errores.dis_id }" @change="errores.dis_id = false">
                                    <option value="" selected disabled>Seleccione una opcin</option>
                                    <option v-for="distrito in distritos" :value="distrito.dis_id">{{ distrito.dis_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.dis_id">
                                    Seleccione un distrito.
                                </span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="calleAvenida">Calle, Avenida, Mz,
                                    S/N</label>
                                <input type="text" class="form-control" id="calleAvenida" placeholder="Av. Peru 2345"
                                    v-model="direccion.dir_calle_av" :class="{ 'has-error': errores.dir_calle_av }"
                                    @input="validarCampo(pnat.dir_calle_av, 'dir_calle_av')">
                                <span class="error-message" v-if="errores.dir_calle_av">
                                    Ingrese una Calle, Avenida, Mz,
                                    S/N.
                                </span>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="d-flex">
                            <div class="ml-auto">
                                <!-- <button class="btn btn-success btn-lg rounded px-4" type="button"
                                    @click="addPart">Guardar</button> -->
                                <button class="btn btn-success btn-lg rounded px-4" @click="addPart"> {{
                                    editingIndex === null ? 'Guardar' : 'Actualizar' }} </button>

                                <button class="btn btn-secondary btn-lg rounded mx-2" type="button"
                                    @click="anteriorParte"><i data-feather="arrow-left"></i> Anterior</button>
                                <button class="btn btn-primary btn-lg rounded" type="button" @click="siguienteParte">
                                    Siguiente<i data-feather="arrow-right"></i>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- <div class="card-footer border-top-0">
                    <div class="row">
                        <div class="d-flex">
                            <div class="ml-auto">
                                <button class="btn btn-secondary btn-lg rounded" type="button" @click="anteriorParte"><i
                                        data-feather="arrow-left"></i> Anterior</button>
                                <button class="btn btn-primary btn-lg rounded ml-2" type="button" @click="siguienteParte">
                                    Siguiente<i data-feather="arrow-right"></i>
                                </button>
                            </div>
                        </div>
                    </div>
                </div> -->
            </div>
        </div>
        <!-- Modal lista de demandantes/demandados -->
        <div v-if="showModal" class="modal" @click="cerrarModal">
            <div class="modal-content card border-0" @click.stop>
                <div class="card-header">
                    <h5 class="text-center">Lista de Demandantes</h5>
                </div>
                <div class="card-body p-0">
                    <div class="table-responsive">
                        <table class="table" id="table">
                            <thead>
                                <tr>
                                    <th class="pl-4">#</th>
                                    <th>{{ tipo_persona === 'NATURAL' ? 'DNI' : 'RUC' }}</th>
                                    <th>{{ tipo_persona === 'NATURAL' ? 'Nombres' : 'Razon social' }}</th>
                                    <th v-if="tipo_persona === 'NATURAL'">Apellidos</th>
                                    <th v-if="tipo_persona === 'NATURAL'">Telefono</th>
                                    <th>Email</th>
                                    <th>Opciones</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(persona, index) in personas" :key="index">
                                    <td class="pl-4">{{ index + 1 }}</td>
                                    <td>{{ tipo_persona === 'NATURAL' ? persona.nat_dni : persona.jur_ruc }}</td>
                                    <td>{{ tipo_persona === 'NATURAL' ? persona.nat_nombres : persona.jur_razon_social }}
                                    </td>
                                    <td v-if="tipo_persona === 'NATURAL'">{{ getApellidos(persona) }}</td>
                                    <td>{{ tipo_persona === 'NATURAL' ? persona.nat_telefono : persona.jur_telefono }}</td>
                                    <td>{{ tipo_persona === 'NATURAL' ? persona.nat_correo : persona.jur_correo }}</td>
                                    <td>
                                        <a href="javascript:void(0)" class="" @click="editarPersona(persona, index)">
                                            Editar
                                        </a>
                                        <a href="javascript:void(0)" class="ml-2 text-danger"
                                            @click="eliminarPersona(index)">
                                            Eliminar
                                        </a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div class="card-footer border-0 pt-2 pb-4">
                    <div class="d-flex">
                        <button class="btn btn-secondary btn-lg ml-auto px-5 rounded" @click="cerrarModal">Cerrar</button>
                        <!-- <button class="btn btn-primary btn-lg ml-auto rounded px-5">
                            Guardar
                        </button> -->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
import { mapActions } from "vuex";
import feather from 'feather-icons';
import "datatables.net-bs5";
import "datatables.net-bs5/css/dataTables.bootstrap5.min.css";
import "datatables.net-buttons-bs5";
import "datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css";

export default {
    name: 'RegistroProcesal',
    mounted() {
        this.$nextTick(() => {
            feather.replace();
            this.initDataTable();
        });
    },
    props: {
        pn: {},
        pj: {},
        dir: {},
        tipopersona: "",
        procesal: "",
        condicion: ""
    },
    data() {
        return {
            dataTable: null,
            esPersonaNatural: true,
            editingIndex: null,
            departamentos: [],
            provincias: [],
            distritos: [],
            personas: [],
            direccion: {
                dir_calle_av: "",
                dis_id: "",
                pro_id: "",
                dep_id: ""
            },
            pnat: {
                nat_dni: "",
                nat_apellido_paterno: "",
                nat_apellido_materno: "",
                nat_nombres: "",
                nat_telefono: "",
                nat_correo: "",
                dir_dep: "",
                dir_pro: "",
                dir_dis: "",
                dir_calle_av: ""
            },
            pjuc: {
                jur_ruc: "",
                jur_razon_social: "",
                jur_telefono: "",
                jur_correo: "",
                jur_rep_legal: "",
                dir_dep: "",
                dir_pro: "",
                dir_dis: "",
                dir_calle_av: ""
            },
            tipo_persona: "",
            tipo_procesal: "",
            condicion1: "",
            errores: {
                tipo_procesal: false,
                tipo_persona: false,
                condicion1: false,
                nat_dni: false,
                nat_apellido_materno: false,
                nat_apellido_materno: false,
                nat_nombres: false,
                jur_ruc: false,
                jur_razon_social: false,
                dep_id: false,
                pro_id: false,
                dis_id: false,
                dir_calle_av: false

            },
            modalVisible: false,
            showModal: false,
        };
    },
    async created() {
        this.pnat = this.pn;
        this.pjuc = this.pj;
        this.direccion = this.dir;
        this.tipo_persona = this.tipopersona;
        this.condicion1 = this.condicion;
        if (this.tipo_persona == '') {
            this.tipo_persona = "NATURAL";
        } {
            if (this.tipo_persona == 'NATURAL') {
                this.tipo_persona = "NATURAL";
            } else {
                this.tipo_persona = "JURIDICA";
            }
        }
        this.tipo_procesal = this.procesal;
        if (this.tipo_procesal == '') {
            this.tipo_procesal = "DEMANDANTE";
        } {
            if (this.tipo_procesal == 'DEMANDANTE') {
                this.tipo_procesal = "DEMANDANTE";
            } else {
                this.tipo_procesal = "DEMANDADO";
            }
        }
        await this.cargarDepartamentos();
        if (this.direccion.dep_id) {
            await this.cargarProvincias();
        }

        if (this.direccion.pro_id) {
            await this.cargarDistritos();
        }
    },
    methods: {
        ...mapActions(['get', 'post']),

        limpiarCampos(objeto) {
            for (let propiedad in objeto) {
                if (objeto.hasOwnProperty(propiedad)) {
                    objeto[propiedad] = "";
                }
            }
        },
        initNatural() {
            const { dep_id, pro_id, dis_id, dir_calle_av } = this.direccion || {};
            this.pnat = { dir_dep: dep_id, dir_pro: pro_id, dir_dis: dis_id, dir_calle_av };
        },

        initLegal() {
            if (this.direccion) {
                const { dep_id, pro_id, dis_id, dir_calle_av } = this.direccion;
                this.pjuc = { dir_dep: dep_id, dir_pro: pro_id, dir_dis: dis_id, dir_calle_av };
            }
        },

        addPart() {
            const isEditing = this.editingIndex !== null;
            if (!this.validarCampos()) {
                return console.error('debe completar todos los campos');
            }
            if (!isEditing) {
                const newPerson = this.tipo_persona === 'NATURAL' ? { ...this.pnat } : { ...this.pjuc };
                this.personas.push(newPerson);
            } else {
                this.personas[this.editingIndex] = { ...(this.tipo_persona === 'NATURAL' ? this.pnat : this.pjuc) };
                this.editingIndex = null;
            }
            this.limpiarCampos(this.tipo_persona === 'NATURAL' ? this.pnat : this.pjuc);
            this.limpiarCampos(this.direccion);
        },


        getApellidos(persona) {
            return `${persona.nat_apellido_paterno} ${persona.nat_apellido_materno}`;
        },

        editarPersona(persona, index) {
            this.editingIndex = index;
            this.cerrarModal();

            this.tipo_persona === 'NATURAL'
                ? (this.pnat = { ...persona })
                : (this.pjur = { ...persona });

            this.editando = true;
        },

        eliminarPersona(index) {
            this.personas.splice(index, 1);
        },

        async cargarDepartamentos() {
            try {

                const response = await this.get({
                    url: this.$store.getters.get__url + "/department",
                    token: this.$store.getters.get__token,
                    params: {}
                });
                this.departamentos = response.data;
                this.provincias = [];
                this.distritos = [];
            } catch (error) {
                console.error("Error al cargar departamentos:", error);
            }
        },
        async cargarProvincias() {
            if (this.direccion.dep_id) {
                try {

                    const response = await this.post({
                        url: this.$store.getters.get__url + "/department/provincias",
                        token: this.$store.getters.get__token,
                        params: {
                            dep_id: this.direccion.dep_id,
                        }
                    });
                    this.provincias = response.data;
                    this.distritos = [];
                    this.validarCampo(this.direccion.dep_id, 'departamento');
                    this.errores.dep_id = false;
                } catch (error) {
                    console.error("Error al cargar provincias:", error);
                }
            }
        },
        async cargarDistritos() {
            if (this.direccion.pro_id) {
                try {
                    const response = await this.post({
                        url: this.$store.getters.get__url + "/department/distritos",
                        token: this.$store.getters.get__token,
                        params: {
                            pro_id: this.direccion.pro_id,
                        }
                    });
                    this.distritos = response.data;
                    this.validarCampo(this.direccion.pro_id, 'provincia');
                    this.errores.pro_id = false;
                } catch (error) {
                    console.error("Error al cargar distritos:", error);
                }
            }
        },

        validarCampo(valor, propiedadError) {
            this.$set(this.errores, propiedadError, valor === "");
        },

        validarPersona() {
            const campos = this.tipo_persona === 'NATURAL'
                ? ['nat_dni', 'nat_apellido_paterno', 'nat_apellido_materno', 'nat_nombres']
                : ['jur_ruc', 'jur_razon_social'];

            campos.forEach(campo => this.validarCampo(this.tipo_persona === 'NATURAL' ? this.pnat[campo] : this.pjuc[campo], campo));

            const hayErrores = campos.some(campo => this.errores[campo]);

            return !hayErrores;
        },


        validarDireccion() {
            const campos = ['dep_id', 'pro_id', 'dis_id', 'dir_calle_av'];
            campos.forEach(campo => this.validarCampo(this.direccion[campo], campo));
            const hayErrores = campos.some(campo => this.errores[campo]);

            return !hayErrores;
        },

        validarCampos() {
            return this.validarPersona() && this.validarDireccion();
        },

        anteriorParte: function () {

            this.$emit('anteriorParte');
            this.$emit('guardarpn', this.pnat);
            this.$emit('guardarpj', this.pjuc);
            this.$emit('guardardir', this.direccion);
            this.$emit('guardartipopersona', this.tipo_persona);
            this.$emit('guardarprocesal', this.tipo_procesal);
            this.$emit('guardarcondicion', this.condicion1);

        },
        siguienteParte: function () {
            if (this.personas.length === 0) {
                return console.info('debe registrar por lo menos una persona');
            }
            this.$emit('siguienteParte');
            this.$emit('guardarpn', this.pnat);
            this.$emit('guardarpj', this.pjuc);
            this.$emit('guardardir', this.direccion);
            this.$emit('guardartipopersona', this.tipo_persona);
            this.$emit('guardarprocesal', this.tipo_procesal);
            this.$emit('guardarcondicion', this.condicion1);
            console.log(this.tipo_procesal);
        },

        mostrarModal() {
            this.initDataTable();
            this.showModal = true;
        },

        cerrarModal() {
            this.showModal = false;
        },

        initDataTable() {
            this.$nextTick(() => {
                this.dataTable = $("#table").DataTable({
                    language: {
                        url: "//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json",
                    },
                });
                feather.replace();
            });
        },


    },
};
</script>
