<template>
    <section class="pc-container pb-4">
        <div class="pcoded-content">
            <!-- <div class="page-header">
                <div class="page-block">
                    <div class="row align-items-center">
                        <div class="col-md-6">
                            <div class="page-header-title">
                                <h5 class="m-b-10">Expedientes</h5>
                            </div>
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <router-link to="/sisge">Inicio</router-link>
                                </li>
                                <li class="breadcrumb-item">Registrar nuevo expediente</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div> -->
            <div class="row mb-4">
                <div class="col-xl-12">
                    <h5 class="text-dark fw-bold">Registro de Nuevo Expediente</h5>
                </div>
            </div>
            <!-- Parte 1: Datos Generales -->
            <RegistroGeneral v-if="currentPart === 1" :exp="exp" @siguienteParte="siguienteParte"
                @guardardatosgenerales="guardardatosgenerales" />

            <!-- Parte 2: Partes Procesales -->
            <RegistroProcesal v-if="currentPart === 2" :pn="pn" :pj="pj" :dir="dir" :tipopersona="tipopersona"
               :condicion="condicion" :procesal="procesal" @guardarpn="guardarpn" @guardarpj="guardarpj" @guardardir="guardardir"
                @anteriorParte="anteriorParte" @siguienteParte="siguienteParte" @guardartipopersona="guardartipopersona"
                @guardarprocesal="guardarprocesal" @guardarcondicion="guardarcondicion" />

            <!-- Parte 3: Asignacin de Abogado -->
            <RegistroAsignacion v-if="currentPart === 3" :abo_id="abo_id" :abo_name="abo_name" @guardaraboid="guardaraboid"
                @guardaraboname="guardaraboname" @InsertData="InsertData" @anteriorParte="anteriorParte" />
        </div>
    </section>
</template>
  
<script>
import { mapActions } from "vuex";
import RegistroGeneral from './Registro/RegistroGeneral.vue';
import RegistroProcesal from './Registro/RegistroProcesal.vue';
import RegistroAsignacion from './Registro/RegistroAsignacion.vue';

export default {
    name: 'Registro',
    components: {
        RegistroGeneral,
        RegistroProcesal,
        RegistroAsignacion
    },
    data() {
        return {
            currentPart: 1,
            exp: {
                exp_numero: "",
                exp_fecha_inicio: "",
                exp_pretencion: "",
                exp_materia: "",
                exp_distrito_judicial: "",
                exp_instancia: "",
                exp_especialidad: "",
                exp_monto_pretencion: "",
                exp_juzgado: "",
                exp_estado_proceso: "",
                exp_costos: "",
                exp_monto_ejecucion1: "",
                exp_monto_ejecucion2: "",
                exp_interes1: "",
                exp_interes2: "",
            },
            pn: {
                nat_dni: "",
                nat_apellido_paterno: "",
                nat_apellido_materno: "",
                nat_nombres: "",
                nat_telefono: "",
                nat_correo: ""
            },
            pj: {
                jur_ruc: "",
                jur_razon_social: "",
                jur_telefono: "",
                jur_correo: "",
                jur_rep_legal: ""
            },
            dir: {
                dir_calle_av: "",
                dis_id: "",
                pro_id: "",
                dep_id: ""
            },
            abo_id: "",
            abo_name: "",
            tipopersona: "",
            procesal: "",
            condicion:""

        };
    },
    methods: {
        ...mapActions(['get', 'post']),
        siguienteParte() {
            if (this.currentPart < 3) {
                this.currentPart++;
            }
        },
        anteriorParte() {
            if (this.currentPart > 1) {
                this.currentPart--;
            }
        },
        guardardatosgenerales(data) {
            this.exp = data;
        },
        guardarpn(data) {
            this.pn = data;
        },
        guardarpj(data) {
            this.pj = data;
        },
        guardardir(data) {
            this.dir = data;
        },
        guardartipopersona(datos) {
            this.tipopersona = datos;
        },
        guardarcondicion(datos) {
            this.condicion = datos;
        },
        guardarprocesal(datos) {
            this.procesal = datos;
        },
        guardaraboid(data) {
            console.log('idabogado');
            this.abo_id = data;
            console.log(this.abo_id);
        },
        guardaraboname(data) {
            console.log('nameabogado');
            this.abo_name = data;
            console.log(this.abo_name);
        },

        InsertData: function () {
            //let loadingAlert;
            console.log('insertando datos...');
            console.log(this.procesal);
            try {
                // loadingAlert = Swal.fire({
                //     title: "Cargando",
                //     text: "Registrando expediente, por favor, espera...",
                //     showConfirmButton: false,
                //     allowOutsideClick: false,
                //     didOpen: () => Swal.showLoading()
                // });
                this.post({
                    url: this.$store.getters.get__url + "/proceeding/registrarcaso",
                    token: this.$store.getters.get__token,
                    params: {
                        exp: this.exp,
                        pj: this.pj,
                        pn: this.pn,
                        dir: this.dir,
                        tipopersona: this.tipopersona,
                        abo_id: this.abo_id,
                        procesal: this.procesal,
                        condicion:this.condicion
                    }
                })
                    .then((response) => {
                        if (response.state == 0) {
                            console.log(response);
                            this.limpiar();
                            this.$router.push({ path: '/sisge/expediente/busqueda' })
                        } else {
                            alert('Datos  no Registrados');
                            console.log(response);
                        }
                    })
                    .catch((errors) => {
                        console.log(errors);
                        alert('Datos  no Registrados');
                    });
            } catch (error) {
                // Swal.fire({
                //     title: "Error al cargar el archivo",
                //     text: "Ha ocurrido un error al cargar el archivo.",
                //     icon: "error"
                // });

                // if (loadingAlert) loadingAlert.close();

                console.error(error);
            }

        },
        limpiar: function () {
            this.exp.exp_numero == "";
            this.exp.exp_fecha_inicio == "";
            this.exp.exp_pretencion == "";
            this.exp.exp_materia == "";
            this.exp.exp_juzgado == "";
            this.exp.exp_monto_pretencion == "";
            this.pn.nat_dni == "";
            this.pn.nat_apellido_paterno == "";
            this.pn.nat_apellido_materno == "";
            this.pn.nat_nombres == "";
            this.pn.nat_telefono == "";
            this.pn.nat_correo == "";
            this.pj.jur_ruc == "";
            this.pj.jur_razon_social == "";
            this.pj.jur_telefono == "";
            this.pj.jur_correo == "";
            this.pj.jur_rep_legal == "";
            this.dir.dir_calle_av == "";
            this.dir.dis_id == "";
            this.dir.pro_id == "";
            this.dir.dep_id == "";
            this.abo_id == "";
            this.abo_name == "";
            this.tipoPersona == "";
            this.tipoParte == "";
        }
    },
};
</script>